var bitmap = null;
export default {
	data() {
		return {
			title: 'map',
			latitude: 39.909,
			longitude: 116.39742,
			covers: [
				{
					latitude: 39.9085,
					longitude: 116.39747,




					iconPath: '../../../static/location.png'

				}
			]
		};
	},
	onReady() {
		uni.getLocation({
			type: 'gcj02',
			success: res => {
				this.latitude = res.latitude;
				this.longitude = res.longitude;
				this.covers = [
					{
						latitude: res.latitude,
						longitude: res.longitude,




						iconPath: '../../../static/location.png'

					}
				];
			}
		});
	},
	onShow() {
		var icon = plus.nativeObj.View.getViewById("icon");
		if (icon) {
			icon.show();
		}

	},
	onLoad() {
		bitmap = new plus.nativeObj.Bitmap('bmp1');
		bitmap.loadBase64Data(
			'data:image/png;base64,',
			function() {},
			function(e) {}
		);
		
		this.createtab();
		this.createRecruitRect();
	},
	methods: {
		createRecruitRect: function() {
			// 设置水平居中位置
			// var leftPos = Math.ceil((plus.screen.resolutionWidth - 60) / 2);

			var button = new plus.nativeObj.View(
				'button',
				{ bottom: '30px', left: '10%', height: '49px', width: '81%', margin: 'auto', backgroundColor: '#ffffff', borderRadius: '30px' },
				[
					{ tag: 'rect', id: 'rect', color: '#1296db', position: { left: '11%', top: '8px', width: '80%', height: '35px', margin: 'auto' } },
					{ tag: 'font', id: 'font', text: '我要招工', textStyles: { size: '18px', color: '#ffffff' } }
				]
			);
			button.show();
			const currentWebview = this.$mp.page.$getAppWebview();
			button.addEventListener('click',function(e){
				//检查登录，检查实名认证
				uni.navigateTo({
					url:"../index/index",
					success: (res) => {
						
					}
				})
			})
			currentWebview.append(button);
			
		},
		createtab: function() {
			// 设置水平居中位置
			var leftPos = Math.ceil((plus.screen.resolutionWidth - 60) / 2);
			var view = new plus.nativeObj.View('icon', {
				bottom: '19px',
				left: leftPos + 'px',
				width: '60px',
				height: '60px'
			});
			view.drawBitmap(bitmap, {
				tag: 'font',
				id: 'icon',
				src: '/static/app-tabber/app-tabber.png',
				position: {
					top: '0px',
					left: '5px',
					width: '50px',
					height: '100%'
				}
			});
			view.addEventListener(
				'click',
				function(e) {
					uni.switchTab({
						url: '../../pages/index/map'
					});
				},
				false
			);
			view.show();
		}
	}
};
